.item {
    font-size: 1.2rem;
    list-style-type: none;
    padding: 17px 0px;
    border-bottom: 1px solid #eaeaea;
}
  
.checkbox {
    margin-right: 15px;
}

.item button {
    /* font-size: 13px;
    background: #f1f3f4; */
    background: transparent;
    border: none;
    cursor: pointer;
    float: right;
    outline: none;
    /* border-radius: 100px; */
    /* height: 50px;
    width: 50px; */
    margin: 0 0 0 10px;
}

.textInput {
    width: 100%;
    padding: 10px;
    border: 1px solid #dfdfdf;
    border-radius: calc(0.5 * 100px);
    padding: 20px 20px;
    color: darkgrey;
    margin: auto;
    font-size: 18px;
}