* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family:'Courier New', Courier, monospace;
    line-height: 1.4;
    color: rgba(10, 10, 10, 0.39);
    /* background: #fff; */
    height: 100vh;
    font-weight: bold;
    /* background-image: url('../images/sky-quotes.jpg'); */
}

.container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    max-width: 600px;
    margin: 0 auto;
  }
  
  .inner {
    width: 100%;
    padding: 8rem 10px 4rem;
  }
  
  .form-container {
    width: 100%;
    margin-bottom: 20px;
    display: -webkit-flex;
    display: flex;
    border-radius: calc(0.5 * 100px);
    box-shadow: 0 1px 16px 0 rgba(0, 0, 0, 0.38);
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }
  
  .input-text {
    font-size: 1rem;
    /* font-weight: 400; */
    width: 85%;
    padding-right: 5px;
    padding-left: 10px;
    background: transparent;
    /* border-radius: calc(0.5 * 100px); */
  }
  
  .input-text::-webkit-input-placeholder {
    color: #9f9f9f;
  }
  
  .input-text::-ms-input-placeholder {
    color: #9f9f9f;
  }
  
  .input-text::placeholder {
    color: #9f9f9f;
  }
  
  .input-submit {
    background: transparent;
    /* color: #5b5b5b;
    text-transform: capitalize; */
    cursor: pointer;
    /* font-weight: 600; */
    margin-right: 10px;
  }

  .input-submit svg{
    color: darkcyan;
    font-size: 20px;
    margin-top: 2px;
  }
  
  .input-text,
  .input-submit {
    height: 45px;
    outline: none;
    border: none;
  }

  .navBar {
    position: relative;
  }
  
  .navBar button {
    position: fixed;
    left: 20px;
    top: 40px;
    z-index: 10;
    cursor: pointer;
    background: transparent;
    border: none;
  }
  
  .menuNav {
    overflow-y: scroll;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    background: darkcyan;
    height: 100vh;
    width: 0;
    overflow: hidden;
    max-width: 200px;
    z-index: 9;
  }
  
  .menuNav.showMenu {
    width: 100%;
  }
  
  a {
    display: block;
    padding: 10px 20px;
    text-decoration: none;
    color: #3fffd2;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 18px;
    font-family:'Times New Roman', Times, serif;
  }
  
  .menuNav li:first-child {
    margin-top: 7rem;
  }

  li {
    list-style: none;
  }

  .about_class {
    padding: 8rem 40px;
    display: -webkit-flex;
    display: flex;
  }
  
  .list {
    -webkit-flex: 2 1;
            flex: 2 1;
    margin-right: 20px;
  }
  
  .list a {
    color: #9f9f9f;
    text-transform: capitalize;
    padding: 10px 0;
    font-weight: bold;
    font-size: 18px;
    font-family:'Times New Roman', Times, serif;
  }
  
  .list a:hover {
    color: #000;
  }
  
  .content {
    -webkit-flex: 7 1;
            flex: 7 1;
    margin-right: 50px;
  }
  
  .content h1 {
    margin: 15px 0 15px 0;
    line-height: 30px;
    font-size: 30px;
  }
  
  .content p {
    line-height: 25px;
  }
